@import url(http://fonts.googleapis.com/css?family=Roboto:400,100,300,500,700,900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*.test {*/
   /*border-style: dotted;*/
 /*}*/

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media {
  .container{
    max-width: 1270px;
  }
}

html, body {
  height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  margin: 0;
}




.profile_header{
  height: 100vh;
  overflow-x: hidden;
  text-align: center;
  background-color: #20232A;
  overflow-y: hidden;
  color: white;
}

.info-card{
  padding: 35px;
  margin-right: 12px;
  margin-left:12px;

}

.logo {
  width: 100px;
  padding-bottom: 25px;
  height: auto;
  filter: gray;
  /* IE6-9 */
  -webkit-filter: grayscale(1);
  /* Google Chrome, Safari 6+ & Opera 15+ */
  filter: grayscale(1);
  /* Microsoft Edge and Firefox 35+ */
}

.info-hover:hover .logo{
  -webkit-filter: grayscale(0);
  filter: none;
}

.logo-container {
  text-align: center;
}

.info {
  padding-top: 20px;
  background-color: #F5F5F5;
  padding-bottom: 100px;
}

.profile {
  position: absolute;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 10%;
  right: 10%;
  margin-top: 45vh;
  padding-top: 10vh;
  padding-bottom: 10vh;
  /*border-style: dotted;*/
  background-color: #282C34;
}

.info-title {
  text-align: center;
  padding-bottom: 35px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 24px;

}

.profile_text {
  display: inline-block;
  margin-right: 50px;
  margin-left: 50px;
  margin-top: 40px;
  -webkit-transform: translateY(2vw);
          transform: translateY(2vw)
}

.course_list {
  padding-left: 0px;
  color: #777;
  margin-left: 20px;
}


.profile_picture {
  border-radius: 50%;
  display: inline-block;
  width: 300px;
  vertical-align: center;
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%);
}

.right {
  float: right;
}

.portfolio-button-container {
  background-color: #777777;
  height: 250px;
}

.portfolio-button {
  -webkit-transform: translateY(14vh);
          transform: translateY(14vh);
}

.skill-title{
  padding-bottom: 12px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 24px;
}

.skill-badge {
  margin-right: 5px;
  margin-bottom: 5px;
}

.skill-container {
  margin-bottom: 30px;
  text-align: center;

}

.social-logo {
  margin-right: 5px;
  margin-left: 5px;
  color: #FFF;
}

.social-logo:hover{
  margin-right: 5px;
  margin-left: 5px;
  color: #AAAAAA;
}

.footer{
  padding-top: 40px;
  padding-bottom: 30px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 14px;
  text-align: center;
  background-color: #F9F9F9;
}

.portfolio-words{
  font-size: 70px;
}

.project_row {
  border: none;
  padding: 16px 32px;
  padding-top: 40px;
  margin: 4px 4px 4px 0px;
  opacity: 0.5;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.project_row img{
  width: 100px;
  height: auto;
  filter: gray;
  /* IE6-9 */
  -webkit-filter: grayscale(1);
  /* Google Chrome, Safari 6+ & Opera 15+ */
  filter: grayscale(1);
}

.project_row:hover {
  opacity: 1;
}


.project_row:hover img{
  -webkit-filter: grayscale(0);
  filter: none;
}

.portfolio-container{
  background-color: #F9F9F9;
  text-align: center;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
}

.project-container{
  overflow-y: auto;
  overflow-x: hidden;
  height: 60vh;
}

.portfolio-tabs{
  width: 46vw;
  min-width: 400px;
  text-align: center;
  display: inline-block;
  -webkit-align-items: center;
          align-items: center;
}

.photography-gallery-container{
  margin-top: 30px;
  display: inline-block;
  max-width: 85vw;
  overflow-y: scroll;
  max-height: 70vh;
}

.footer-link{
  color: #000000;
  background-image: -webkit-linear-gradient(left, rgba(255,255,255,0) 50%, yellow 50%);
  background-image: linear-gradient(to right, rgba(255,255,255,0) 50%, yellow 50%);
  background-position: -0% 0;
  background-size: 200% auto;
  line-height: 1.15em;
  text-decoration: none;
  -webkit-transition: background-position 0.5s ease-out;
  transition: background-position 0.5s ease-out;
  padding-left: 10px;
  padding-right: 10px;
}

.footer-link:hover{
  background-position: -99.99% 0;
  text-decoration: none;
  color: inherit;
}

.e-mail{
  font-size: 14px;
  letter-spacing: 3px;
  font-weight: lighter;
}
.downArrow{
    position: fixed;
    left: 39vw;
    bottom: -15vh;
    /*border-style: dotted;*/
}

.bounce {
    -webkit-animation: bounce 3s infinite;
    animation: bounce 3s infinite;
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
        transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
        transform: translateY(-15px);
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    40% {
        -webkit-transform: translateY(-30px);
        transform: translateY(-30px);
    }
    60% {
        -webkit-transform: translateY(-15px);
        transform: translateY(-15px);
    }
}
